import React, { Component } from 'react';
import {Nav, Navbar, NavItem, NavLink} from 'reactstrap'
import logo from '../logos/vectorpaint.svg'
import './MainPage.css';
import auth from '../logos/login.png'
import todo from '../logos/todo.png'
import page1 from '../logos/page1.png'
import chat from '../logos/chat.png'
import weather from '../logos/cloud.png'
import resort from '../logos/resort.webp'
import netflix from '../logos/netflix.png'
import food from '../logos/Food.png'
import {Link} from "react-router-dom";
import Stack from "./Stack";


class MainPage extends Component {
    render() {
        return (

            <div className="container1">
                <Navbar className='navbar1' >
                    <Nav >
                        <NavItem >
                            <NavLink href='#home'>Home</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href='#about'>About</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href='#stack'>Stack</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href='#works'>Works</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href='#contacts'>Contacts</NavLink>
                        </NavItem>
                    </Nav>
                </Navbar>
                <section className='SectionHome' id="home">
                    <h1>Front-End React Developer</h1>
                    <div className='justify-content-between '>
                        <Nav className='' >
                            <NavItem>
                                <NavLink  href='https://github.com/Automedon'><i className="fa fa-github display-4 text-white"  aria-hidden="true"/></NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink  href='https://www.linkedin.com/in/frontReactDeveloper/'><i className="fa fa-linkedin display-4 text-white"  aria-hidden="true"/></NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink  href='https://www.codewars.com/users/Valefar'>
                                    <img  src={logo} style={{width:56}} alt="logo" />
                                </NavLink>
                            </NavItem>

                        </Nav>
                    </div>

                </section>
                <section className='    ' id="about">

                    <div className=''>
                        <h2>Position</h2>
                        <p>I am looking for a Middle Front-End React developer position
                        </p>

                        <h2>Experience</h2>
                        <p>I have 1-year experience with React/Redux
                        </p>
                        <br/>
                        <h2>My capability right now</h2>
                        <ul className='justify-content-center text-ul' >
                            <li className="li-item">Mark up mobile-responsive site from a layout</li>
                            <li  className="li-item">Make a working api with axios from the front and express.js server + MongoDB on the backend</li>
                            <li  className="li-item">Write middleware with redux-saga or redux-thunk on the front </li>
                            <li  className="li-item">Send actions through actionsCreators through reducers to the store</li>
                            <li  className="li-item">Representing data from a redux-store to React components</li>
                            <li  className="li-item">Wrap React component in HOC</li>
                            <li  className="li-item">Routing(PrivateRouting) on the page with react-router-dom </li>
                            <li  className="li-item">Make authentication with a jwt token </li>
                            <li  className="li-item">Writing unit tests with Jest</li>
                            <li  className="li-item">And the most important thing here is I can learn any library or framework</li>
                        </ul>
                    </div>
                </section>
                <Stack/>
                <section id="works">
                    <h2>Here some examples of my works</h2>
                    <Nav className='d-flex justify-content-around' >
                        <NavItem className='pr-2 pt-4'>
                            <a className='works-li' href='https://resort-with-nextjs.inzhenerintellect.now.sh'>
                                <img  src={resort} className='icon'  alt="logo" />
                                <br/>
                                NEXT.js
                            </a>
                        </NavItem>
                        <NavItem className='pr-2 pt-4'>
                        <a className='works-li' href='https://netflix-clone.inzhenerintellect.now.sh'>
                            <img  src={netflix} className='icon'  alt="logo" />
                            <br/>
                            Netflix
                        </a>
                    </NavItem>
                        <NavItem className='pr-2 pt-4'>
                            <a className='works-li' href='https://automedon.github.io/ReactFood2Fork/'>
                                    <img  src={food} className='icon'  alt="logo" />
                                <br/>
                                Recipe App
                            </a>
                        </NavItem>
                      <NavItem className='pr-2 pt-4'>
                        <Link className='works-li' to='/weatherApp'>
                          <img  src={weather} className='icon'  alt="logo" />
                          <br/>
                          WheatherApp
                        </Link>
                      </NavItem>
                        <NavItem className='pr-2 pt-4'>
                            <Link className='works-li' to='/todolist'>
                                <img  src={todo} className='icon'  alt="logo" />
                                <br/>
                                Todo List
                            </Link>
                        </NavItem>
                      <NavItem className='pr-2 pt-4'>
                        <Link className='works-li' to='/landingpage1'>
                          <img  src={page1} className='icon'  alt="logo" />
                          <br/>
                          Landing Page
                        </Link>
                      </NavItem >
                      <NavItem className='pr-2 pt-4'>
                        <a className='works-li' href='https://aqueous-river-29369.herokuapp.com'>
                          <img  src={auth} className='icon'  alt="logo" />
                          <br/>
                          Authorization <br/> deployed <br/> on heroku
                        </a>
                      </NavItem>
                      <NavItem className='pr-2 pt-4'>
                        <a className='works-li' href='https://automedon.github.io/simpleChat/'>
                          <img  src={chat} className='icon'  alt="logo" />
                          <br/>
                          Simple Chat
                        </a>
                      </NavItem>
                    </Nav>
                </section>
                <section id="contacts">
                    <h2>Email:</h2>
                    <p>inzhener.intellect@gmail.com</p>
                </section>
            </div>

        );
    }
}

export default MainPage;
