import React, { Component } from 'react';
import {Nav, NavItem, NavLink} from 'reactstrap'

import react from '../logos/react.svg'
import redux from '../logos/redux.svg'
import html from '../logos/480px-HTML5_logo_and_wordmark.svg.png'
import css from '../logos/CSS.3.svg'
import js from '../logos/Javascript_badge.svg.png'
import jest from '../logos/jest.png'
import bootstrap from '../logos/bootstrap.png'
import webpack from '../logos/webpack.png'
import RESTAPI from '../logos/RESTAPI.png'
import node from '../logos/node_js.png'
import express from '../logos/express.png'
import mongoose from '../logos/mongoose.jpg'
import mongoDB from '../logos/mongodb.png'
import jwt from '../logos/jwt.png'
import nextjs from '../logos/1280px-Nextjs-logo.svg.png'
import './MainPage.css';


class MyComponent extends Component {
  render() {
    return (
      <section id="stack" >
        <div className='stack'>
          <h1>My current technology stack</h1>
          <h2>Front-End</h2>
          <Nav className='justify-content-center icon-ul' >
            <NavItem>
              <NavLink className='item'  href='https://www.w3.org/html/'>
                <img  src={html} className='icon'  alt="logo" />
                <br/>
                HTML5
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://www.w3.org/css/'>
                <img  src={css} className='icon'  alt="logo" />
                <br/>
                CSS3
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://www.w3schools.com/js/'>
                <img  src={js} className='icon'  alt="logo" />
                <br/>
                JavaScript
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://reactjs.org'>
                <img  src={react} className='icon'  alt="logo" />
                <br/>
                React
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://redux.js.org'>
                <img  src={redux} className='icon'  alt="logo" />
                <br/>
                Redux
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://getbootstrap.com'>
                <img  src={bootstrap} className='icon'  alt="logo" />
                <br/>
                Bootstrap
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://jestjs.io'>
                <img  src={jest} className='icon'  alt="logo" />
                <br/>
                Jest
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://webpack.js.org'>
                <img  src={webpack} className='icon'  alt="logo" />
                <br/>
                Webpack
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://www.restapitutorial.com/lessons/httpmethods.html'>
                <img  src={RESTAPI} className='icon'  alt="logo" />
                <br/>
                RESTAPI
              </NavLink>
            </NavItem>
              <NavItem>
                  <NavLink className='item' href='https://nextjs.org'>
                      <img  src={nextjs} className='icon'  alt="logo" />
                      <br/>
                      NEXT.js
                  </NavLink>
              </NavItem>
          </Nav>
          <br/>
          <h2>Back-End</h2>
          <Nav className='justify-content-center' >
            <NavItem>
              <NavLink className='item' href='https://nodejs.org/en/'>
                <img  src={node} className='icon-node'  alt="logo" />
                <br/>
                Node.js
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='http://expressjs.com/'>
                <img  src={express} className='icon'  alt="logo" />
                <br/>
                Express.js
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://mongoosejs.com'>
                <img  src={mongoose} className='icon-mongoose'  alt="logo" />
                <br/>
                Mongoose
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://www.mongodb.com'>
                <img  src={mongoDB} className='icon-mongo'  alt="logo" />
                <br/>
                MongoDB
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className='item' href='https://jwt.io'>
                <img  src={jwt} className='icon'  alt="logo" />
                <br/>
                JWT
              </NavLink>
            </NavItem>
              <NavItem>
                  <NavLink className='item' href='https://nextjs.org'>
                      <img  src={nextjs} className='icon'  alt="logo" />
                      <br/>
                      NEXT.js
                  </NavLink>
              </NavItem>
          </Nav>
        </div>
      </section>
    );
  }
}



export default MyComponent;
